export class SQLSplitterService {
  constructor() {
    this.splitRegex = /\s*;\s*/g;
    this.maxChunkSize = 90 * 1024; // 90 KB default chunk size
  }

  getByteSize(str) {
    return new TextEncoder().encode(str).length;
  }

  generateValidationQuery(tableName) {
    return `\n\nSELECT COUNT(*) FROM ${tableName} WHERE updated_time >= SYSDATE - INTERVAL '10' MINUTE;`;
  }

  cleanSql(sql) {
    // Remove all SET DEFINE OFF statements and SELECT statements
    let cleanedSql =
      sql
        .split(this.splitRegex)
        .filter((stmt) => {
          const trimmedStmt = stmt.trim().toLowerCase();
          return !trimmedStmt.startsWith("select") && !trimmedStmt.startsWith("set define off");
        })
        .map((stmt) => stmt.trim())
        .filter((stmt) => stmt) // Remove empty statements
        .join(";\n\n") + ";"; // Add semicolon after each statement and at the end

    return cleanedSql;
  }

  splitSql(sql, tableName) {
    const chunks = [];
    let oversizedChunksCount = 0;
    const defineOff = "SET DEFINE OFF;\n\n";
    const validationQuery = this.generateValidationQuery(tableName);

    // Clean the SQL first
    const cleanedSql = this.cleanSql(sql);

    // Split the SQL into individual statements
    const statements = cleanedSql
      .split(this.splitRegex)
      .map((stmt) => stmt.trim())
      .filter((stmt) => stmt);

    let currentChunk = defineOff;
    let currentSize = this.getByteSize(defineOff);
    const validationSize = this.getByteSize(validationQuery);

    for (const statement of statements) {
      const statementSize = this.getByteSize(statement);

      // If a single statement is larger than maxChunkSize, create a chunk with just this statement
      if (statementSize > this.maxChunkSize) {
        chunks.push(defineOff + statement + ";" + validationQuery);
        oversizedChunksCount++;
        continue;
      }

      // If adding this statement would exceed maxChunkSize, start a new chunk
      if (currentSize + statementSize + validationSize > this.maxChunkSize) {
        if (currentChunk !== defineOff) {
          chunks.push(currentChunk.trim() + ";" + validationQuery);
        }
        currentChunk = defineOff + statement;
        currentSize = this.getByteSize(defineOff) + statementSize;
      } else {
        // Add statement to current chunk
        currentChunk = currentChunk === defineOff ? defineOff + statement : `${currentChunk};\n\n${statement}`;
        currentSize += statementSize;
      }
    }

    // Add the last chunk if it exists
    if (currentChunk !== defineOff) {
      chunks.push(currentChunk.trim() + ";" + validationQuery);
    }

    return {
      chunks,
      oversizedChunksCount,
    };
  }
}
